<template>
<div id="list" class="mylist" v-infinite-scroll="loadMore"
	  infinite-scroll-disabled="loading"
	  infinite-scroll-distance="10">
	  <article class="media" v-on:click="godetail(item)" v-for="item in items">
	    <div class="media-content">
	      <div class="content">
	        <h4 class="title">{{item.title}}</h4>
	        <section class="abstract">{{item.summary}}</section>
	        <span class="publish_time">{{item.created_at}}</span>
	      </div>
	    </div>
	    <div class="media-right">
	      <figure class="image is-64x64">
	        <img v-bind:src="item.logo" alt="Image">
	      </figure>
	    </div>
	  </article>
</div>
</template>
<script>
	import router from '../router/index'

	export default {
		name:'list',
		data() {
        	return {
        		items: [],
        		page:1,				
				loading:false   	 
      		};
		},
		mounted(){
			let that = this
			this.$http.get('http://summer.limaogo.com/node/?page=1').then(response => {
			    if (response.status == 200) {
			    	that.items = response.data
			    }
			}, response => {
			    // error callback
			})
		},
		methods:{
		loadMore() {
			var that = this;
			that.loading = true;
			that.page++;
			that.$http.get(`http://summer.limaogo.com/node/?page=${that.page}`)
			  .then(function (response) {
			  	 if (response.status == 200) {
			   		that.loading = false
			   		that.items = that.items.concat(response.data)
				}
			  })
			  .catch(function (error) {
			    console.log(error);
			  });
		},
		  godetail(item){
			sessionStorage.setItem('detail',JSON.stringify(item));
		  	router.push({name:'detail',params:{id:1}})
		  }
		}
	}
</script>
<style scoped>
	.mylist{
		background:#fff;
		padding: 1.2rem 0.8rem 4rem;
		margin-top: 1px;
	}
	.mylist .content .title{
		    font-size: 1.1em;
		    margin-bottom: 0.4rem
	}
	.mylist .content .abstract{font-size: 0.9em;color: #999;height: 42px;overflow: hidden;}
	.mylist .content .publish_time{color: #aaa;font-size: 0.8em}
</style>


