<template>
	<div class='detail'>
		<my-header />
		<div class="container is-fullhd">
			<div class="content">
			  <h2>{{data.title}}</h2>			  
			  <p>{{data.created_at}}</p>
			  <div v-html="data.content">
			  </div>
			</div>
		</div>
	</div>
</template>
<script>
	import header from './header.vue'
	export default{
		name:'detail',
		data(){
			return {
				data:{}
			}
		},
		components:{
			'my-header':header
		},
		mounted(){			
			this.data = JSON.parse(sessionStorage.getItem('detail'));
		}
	}
</script>
<style scoped>
    .content{padding: 1.2rem 0.8rem 5rem;}
</style>


